import Vue from 'vue'; //class
import axios from 'axios';

// 配置axios
//拦截器
	// 请求拦截
	axios.interceptors.request.use(function (config) {
	    // 做一些事
		// config.headers.token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VybmFtZSI6ImFkbWluIiwiX2lkIjoiNjE4N2Q0NTZiZGYzNjczMTkxMzc1ODA5IiwiaWF0IjoxNjM2NzcwMDk3LCJleHAiOjE2MzY4NTY0OTd9.XXejADnNw53QtteIWoc55Pe7i-qIIvx0rhbyw6OtfoU';
		let user = window.localStorage.getItem('user');//{err:0,msg:'xxx',data:{},token:xxx}
		let token = user ? JSON.parse(user).token : ''
		config.headers.token = token;
		config.baseURL='/api/';
		
		// 控制loading显示
	    return config;
	  }, function (error) {
	    // Do something with request error
	    return Promise.reject(error);
	  });
	
	// 响应拦截
	axios.interceptors.response.use(function (response) {
	   
	    // 控制loading隐藏
		
		
	    return response.data;
	  }, function (error) {
	    // Any status codes that falls outside the range of 2xx cause this function to trigger
	    // Do something with response error
	    return Promise.reject(error);
	  });



Vue.prototype.$axios = axios;
export default axios;