<template>
  <div class="content">
    <p class="fhbtn"><a href="javascript:history.go(-1);"></a></p>
    <h1 @click="$refs.typefile.click()"></h1>
    <input type="file" ref="typefile" style="display: none;">
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link to="/login">登录</router-link>
        <span></span>
        <router-link to="">注册</router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
        <input style="text-indent: 5px;" type="text" v-model="username"/>
        <span>帐号</span>
      </li>
      <li>
        <input style="text-indent: 5px;" type="text" v-model="password"/>
        <span>密码</span>
      </li>
      {{mess}}
    </ul>
    <div class="footbox">
      <input type="button" value="注 册" class="login-btn" @click="reg"/>
      <a href="javascript:;" class="tishi">忘记密码？</a>
    </div>
  </div>
</template>

<script>
  export default {
    name:'reg',
    props:{},
    data(){
      return {
        username:'',
        password:'',
        mess:''
      }
    },
    components:{},
    mounted(){},
    updated(){},
    methods:{
      reg(){
        //1.获取头像  ,传给服务器  (formData)
        let data = new FormData();
        data.append('username',this.username);
        // data.set('username',value)//修改
        // data.has('key') true/false
        // data.get('username')// alex
        // data.getAll();//{username,password}
        data.append('password',this.password);
        this.$refs.typefile.files.length && data.append('icon',this.$refs.typefile.files[0])
        
        this.$axios({
          url:'/reg',
          method:'post',
          data
        }).then(
          res=>{
            if(res.err===0){
              //3.注册成功 登录 跳转到登录
              this.$router.push('/login')
            }else if(res.err===1){
              //2.注册失败  渲染错误描述信息到页
              this.mess = res.msg;
            }
          }
        )
        
        
      }
    },
    computed: {
    }
  }
</script>

