import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import home from '../pages/home.vue'
import cart from '../pages/cart.vue'
import list from '../pages/list.vue'
import list1 from '../pages/list/list1.vue'
import list2 from '../pages/list/list2.vue'
import list3 from '../pages/list/list3.vue'
import list4 from '../pages/list/list4.vue'
import list5 from '../pages/list/list5.vue'
import list6 from '../pages/list/list6.vue'
import user from '../pages/user.vue'
import reg from '../pages/reg.vue'
import login from '../pages/login.vue'
import NoPage from '../pages/no-page.vue'
import detail from '../pages/detail.vue'
import axios from './axios.js';

let routes = [
  {path:'/home', component:home},
  {path:'/home/:_id', component:detail,name:'home-detail',meta:{path:'detail'}},
  {path:'/cart', component:cart},
  {path:'/list', component:list,
	children:[
		{path:'/list1', component:list1},
		 {path:'/list1/:_id', component:detail,name:'list-detail1',meta:{path:'detail'}},
		{path:'/list2', component:list2},
		 {path:'/list2/:_id', component:detail,name:'list-detail2',meta:{path:'detail'}},
		{path:'/list3', component:list3},
		 {path:'/list3/:_id', component:detail,name:'list-detail3',meta:{path:'detail'}},
		{path:'/list4', component:list4},
		 {path:'/list4/:_id', component:detail,name:'list-detail4',meta:{path:'detail'}},
		{path:'/list5', component:list5},
		 {path:'/list5/:_id', component:detail,name:'list-detail5',meta:{path:'detail'}},
		{path:'/list6', component:list6},
		 {path:'/list6/:_id', component:detail,name:'list-detail6',meta:{path:'detail'}},
	],
  },
  {path:'/user', component:user},
  {path:'/login', component:login},
  {path:'/reg', component:reg},
  {path:'/',redirect:'/home'},
  {path:'*', component:NoPage},
];

let router = new VueRouter({
  routes,
  mode:'history'
})

// 路由守卫
router.beforeEach((to,from,next)=>{
  //只校验token  -->组件还需读自身数据
  let path = to.path;
  if(path.includes('/login') || path.includes('/reg')){
    next()
  }else{
    axios({
      url:'/is-token',
    }).then(
      res=>{
        if(res.err===2 ){
          // console.log('守卫',res)
          next(`/login?path=${to.fullPath}`)
        }else{
          next()
        }
      }
    )
  }
})

export default router;