<template>
  <div class="app">
    <navbar v-show="bNav"/>
    <router-view/>
    <tabbar v-show="bFoot"/>
  </div>
</template>

<script>
  import navbar from '../components/navbar.vue'
  import tabbar from '../components/tabbar.vue'
  import home from '../pages/home.vue'	
  export default {
    name:'app',
    props:{},
    data(){
      return {
        bNav:true,
        bFoot:true
      }
    },
    components:{
		navbar,tabbar,home
	},
	 watch:{
	      $route(val){
	        // console.log('...',val.path);
	        let path = val.path;
	        let path2 = val.meta.path;
	        if(/home/.test(path)) {
	          this.bNav=true;
	          this.bFoot=true;
	        }
	        if(/login|reg/.test(path) || /detail/.test(path2)) {
	          this.bNav=false;
	          this.bFoot=false;
	        }
	        if(/cart|user/.test(path)){
	          this.bNav=false;
	          this.bFoot=true;
	        }
			if(/list/.test(path)) {
			  this.bNav=false;
			  this.bFoot=false;
			}
	      }
	    },
    mounted(){},
    updated(){},
    methods:{},
    computed: {
    }
  }
</script>

<style scoped>
   .app{}
</style>
